import React from "react";

import { Layout, Menu, Affix } from "antd";
import {
  UserOutlined,
  ClusterOutlined,
  GroupOutlined,
} from "@ant-design/icons";
import { NavLink, useLocation } from "react-router-dom";

const { SubMenu } = Menu;
const { Sider } = Layout;

const AppSideBar = () => {
  const [currentLocation, setCurrentLocation] = React.useState<string[]>(["/"]);

  const location = useLocation();

  React.useEffect(() => {
    setCurrentLocation([`${location.pathname}${location.search}`]);
  }, [location]);

  return (
    <Affix offsetTop={64}>
      <Sider width={200} className="site-layout-background sidebar">
        <Menu
          mode="inline"
          className="sidebar__menu"
          selectedKeys={currentLocation}
        >
          <SubMenu key="sub1" icon={<GroupOutlined />} title="">
            <Menu.Item key="/orders">
              <NavLink to="/orders"> </NavLink>
            </Menu.Item>
            <Menu.Item key="/orders?filter=active">
              <NavLink to="/orders?filter=active"> </NavLink>
            </Menu.Item>
            <Menu.Item key="/orders?filter=archived">
              <NavLink to="/orders?filter=archived"> </NavLink>
            </Menu.Item>
            <Menu.Item key="/orders?filter=deleted">
              <NavLink to="/orders?filter=deleted"> </NavLink>
            </Menu.Item>

            <Menu.Item key="/orders/create">
              <NavLink to="/orders/create"> </NavLink>
            </Menu.Item>
          </SubMenu>

          <SubMenu key="sub3" icon={<UserOutlined />} title="">
            <Menu.Item key="/customcontractorsers">
              <NavLink to="/contractors"> </NavLink>
            </Menu.Item>
            <Menu.Item key="/contractors?filter=customers">
              <NavLink to="/contractors?filter=customers"></NavLink>
            </Menu.Item>
            <Menu.Item key="/contractors?filter=supplier">
              <NavLink to="/contractors?filter=supplier"></NavLink>
            </Menu.Item>
            <Menu.Item key="/contractors?filter=deleted">
              <NavLink to="/contractors?filter=deleted"></NavLink>
            </Menu.Item>
            <Menu.Item key="/contractors/create">
              <NavLink to="/contractors/create"> </NavLink>
            </Menu.Item>
          </SubMenu>

          <SubMenu key="sub4" icon={<ClusterOutlined />} title="">
            <Menu.Item key="/categories">
              <NavLink to="/categories"> </NavLink>
            </Menu.Item>
            <Menu.Item key="/categories/deleted">
              <NavLink to="/categories/deleted"> </NavLink>
            </Menu.Item>
            <Menu.Item key="/categories/create">
              <NavLink to="/categories/create"> </NavLink>
            </Menu.Item>
          </SubMenu>
        </Menu>
      </Sider>
    </Affix>
  );
};

export default React.memo(AppSideBar);
