import { Descriptions } from "antd";
import React from "react";
import { Control, FieldError } from "react-hook-form";
import { NavLink } from "react-router-dom";
import { DescriptionOrderFormData, OrderType } from "../../../models/Orders";
import { formatDate } from "../../../utils/formatter";
import OrderEditableField from "./OrderEditableField";

type OrderDescriptionPropsType = {
  order: OrderType;
  editMode: boolean;
  errors: {
    title?: FieldError;
    totalSum?: FieldError;
  };
  control: Control<DescriptionOrderFormData, object>;
};

const OrderDescription: React.FC<OrderDescriptionPropsType> = ({
  order,
  editMode,
  errors,
  control,
}) => {
  return (
    <Descriptions size="small" column={3}>
      <Descriptions.Item label="">
        {formatDate(order.creationDate)}
      </Descriptions.Item>
      <Descriptions.Item label="">
        <NavLink to={`/contractors/${order.customerId}`}></NavLink>
      </Descriptions.Item>
      <Descriptions.Item label="">
        <OrderEditableField
          defaultValue={order.comment}
          editMode={editMode}
          control={control}
          error={errors.title}
          fieldName="comment"
          placeholder=""
        />
      </Descriptions.Item>

      <Descriptions.Item label="">
        {formatDate(order.actualDate)}
      </Descriptions.Item>
      <Descriptions.Item label="">
        <NavLink to={`/orders/${order.id}/chats`}></NavLink>
      </Descriptions.Item>
      <Descriptions.Item label="">
        <OrderEditableField
          isNumberInput
          defaultValue={order.totalSum}
          editMode={editMode}
          control={control}
          error={errors.totalSum}
          fieldName="totalSum"
          placeholder=""
        />
      </Descriptions.Item>
      <Descriptions.Item label=" ">{order.id}</Descriptions.Item>
    </Descriptions>
  );
};

export default OrderDescription;
