import * as yup from "yup";

export const orderSchema = yup.object().shape({
  title: yup
    .string()
    .max(150, "  150 ")
    .required(" "),
  totalSum: yup
    .number()
    .typeError("  ")
    .required(" "),
  comment: yup.string().required(" "),
  description: yup
    .string()
    .typeError("    ")
    .max(5000, "  5000 "),
});

export const supplierSchema = yup.object().shape({
  name: yup
    .string()
    .max(150, "  150 ")
    .required(" "),
  location: yup
    .string()
    .max(50, "  50 ")
    .required(" "),
  phoneNumber: yup //TODO:   
    .number()
    .typeError("   ")
    .min(11, "   ")
    .required(" "),
  description: yup
    .string()
    .typeError("    ")
    .max(5000, "  5000 "),
  address: yup
    .string()
    .max(250, "  250 ")
    .required(" "),
  contactName: yup
    .string()
    .max(150, "  150 ")
    .required(" "),
  webSite: yup.string().max(50, "  50 "),
  // .url("  URL"), //TODO:  ?
  eMail: yup
    .string()
    .max(50, "  50 ")
    .email("  e-mail"),
});

export const otpSchema = yup.object().shape({
  phoneNumber: yup
    .number()
    .typeError("   ")
    .min(11, "   ")
    .required(" "),
  otp: yup
    .string()
    .required(" ")
    .matches(/^[0-9]+$/, "  -")
    .min(4, "  -")
    .max(4, "  -"),
});

export const customerSchema = yup.object().shape({
  name: yup
    .string()
    .max(150, "  150 ")
    .required(" "),
  location: yup
    .string()
    .max(50, "  50 ")
    .required(" "),
  contactName: yup
    .string()
    .max(150, "  150 ")
    .required(" "),
  phoneNumber: yup
    .number()
    .typeError("   ")
    .min(11, "   ")
    .required(" "),
});

export const categorySchema = yup.object().shape({
  name: yup
    .string()
    .max(150, "  150 ")
    .required(" "),
  parentId: yup
    .number()
    .typeError("  ")
    .required(" "),
});
