import { Button, Descriptions, Radio, RadioChangeEvent } from "antd";
import React from "react";
import { Control, FieldError } from "react-hook-form";
import { NavLink } from "react-router-dom";
import {
  ContractorType,
  ContractorTypesEnum,
  CustomerDescrFormDataType,
  SupplierDescrFormDataType,
} from "../../../models/Contractors";
import ContractorEditableField from "./ContractorEditableField";

type ContractorDescriptionPropsType = {
  editMode: boolean;
  handleSelectRegType: (e: RadioChangeEvent) => void;
  toggleShowMap: () => void;
  registeringType: ContractorTypesEnum;
  contractor: ContractorType;
  control: Control<
    SupplierDescrFormDataType | CustomerDescrFormDataType,
    object
  >;
  errors:
    | {
        name?: FieldError;
        phoneNumber?: FieldError;
        contactName?: FieldError;
        location?: FieldError;
        webSite?: FieldError;
        eMail?: FieldError;
        address?: FieldError;
        description?: FieldError;
      }
    | {
        name?: FieldError;
        phoneNumber?: FieldError;
        contactName?: FieldError;
        location?: FieldError;
      };
};
//TODO:      

const ContractorDescription: React.FC<ContractorDescriptionPropsType> = ({
  editMode,
  handleSelectRegType,
  registeringType,
  contractor,
  control,
  errors,
  toggleShowMap,
}) => {
  return (
    <>
      <Descriptions size="small" column={4}>
        <Descriptions.Item label=" ">
          {editMode ? (
            <Radio.Group onChange={handleSelectRegType} value={registeringType}>
              <Radio value={ContractorTypesEnum.SUPPLIER}></Radio>
              <Radio value={ContractorTypesEnum.CUSTOMER}></Radio>
            </Radio.Group>
          ) : contractor.contractorType === ContractorTypesEnum.CUSTOMER ? (
            ""
          ) : (
            ""
          )}
        </Descriptions.Item>
        <Descriptions.Item label=" ">
          <NavLink to="/orders"></NavLink>
        </Descriptions.Item>
        <Descriptions.Item label=" ">
          <ContractorEditableField
            defaultValue={contractor.contactName}
            editMode={editMode}
            control={control}
            error={errors.contactName}
            fieldName="contactName"
            placeholder=" "
          />
        </Descriptions.Item>

        <Descriptions.Item label="">
          <ContractorEditableField
            defaultValue={contractor.location}
            editMode={editMode}
            control={control}
            error={errors.location}
            fieldName="location"
            placeholder=""
          />
        </Descriptions.Item>
        <Descriptions.Item label="">
          <Button size="small" type="link" onClick={toggleShowMap}>
              
          </Button>
        </Descriptions.Item>

        <Descriptions.Item label="">
          <ContractorEditableField
            defaultValue={contractor.contacts.webSite}
            editMode={editMode}
            control={control}
            //@ts-ignore
            error={errors.webSite}
            fieldName="webSite"
            placeholder=""
          />
        </Descriptions.Item>
        <Descriptions.Item label="">
          <ContractorEditableField
            defaultValue={contractor.contacts.eMail}
            editMode={editMode}
            control={control}
            //@ts-ignore
            error={errors.eMail}
            fieldName="eMail"
            placeholder=""
          />
        </Descriptions.Item>
        <Descriptions.Item label="">
          <ContractorEditableField
            defaultValue={contractor.contacts.address}
            editMode={editMode}
            control={control}
            //@ts-ignore
            error={errors.address}
            fieldName="address"
            placeholder=""
          />
        </Descriptions.Item>

        <Descriptions.Item label="">
          <ContractorEditableField
            defaultValue={`+${contractor.phoneNumber}`}
            editMode={editMode}
            control={control}
            error={errors.phoneNumber}
            fieldName="phoneNumber"
            placeholder=""
            maxLength={12}
          />
        </Descriptions.Item>
        <Descriptions.Item label=" ">
          {contractor.id}
        </Descriptions.Item>
      </Descriptions>
    </>
  );
};

export default React.memo(ContractorDescription);
